import React from "react"
import Layout from "../component/layout"
import { graphql } from "gatsby"
import { renderRichText } from "gatsby-source-contentful/rich-text"
import { BLOCKS ,INLINES,MARKS,Document} from "@contentful/rich-text-types"

import SyntaxHighlighter from "react-syntax-highlighter";
import { obsidian } from "react-syntax-highlighter/dist/esm/styles/hljs";

import {
helpers
} from '@contentful/rich-text-types';
export const query = graphql`
  query($slug: String!) {
    contentfulBlogpost(slug: { eq: $slug }) {
      title
      
      images{
        gatsbyImageData
      }
      body {
        raw
        references {
           ... on ContentfulAsset {
            updatedAt(formatString: "MMMM Do, YYYY")
            contentful_id
            __typename
            fluid(maxWidth: 1600) {
              src
              srcSet
            }
          }
        }
      }
    }
  }
`
const find=(source,statment)=>{
  let result=false;
  source.map((data)=>{
    console.log(data,statment);
    if(data.type===statment.type){
      result=true;
    }else {
     result=false
    }
  })
  return result 
}
const Text = ({ children }) => <p className="align-center">{children}</p>
const options = {
  renderMark: {
    // add heading and paragraph padding 
    [MARKS.BOLD]: text => <b>{text}</b>,
    [MARKS.ITALIC]: text => <i>{text}</i>,
    [MARKS.UNDERLINE]: text => <u>{text}</u>,
    [MARKS.CODE]: text => <code>{text}</code>,
  },
  renderNode: {
    [INLINES.HYPERLINK]: ({ data }, children) => (
      <a
        className="blog_link"
        href={data.uri}
       
      >{children}</a>
    ),
    [BLOCKS.PARAGRAPH]: (node, children) => {
      if (
        node.content.length === 1 &&
        find(node.content[0].marks, { type: 'code' })
      ) {
       
        return <pre><SyntaxHighlighter
        language="javascript"
        style={obsidian}
        showLineNumbers >{node.content[0].value}</SyntaxHighlighter></pre>;
      }
    
      return <p>{children}</p>;
    },

    [BLOCKS.EMBEDDED_ASSET]: node => {
      console.log(node)
      return (
        <>
          <h2>Embedded Asset</h2>
          <pre>
          
          
            <img
            className="blog-container-images"
              src={node.data.target.fluid.src}
              alt={node.data.target.title}
            />
            <a />
          </pre>
        </>
      )
    },
    
  },
}
export default function Blog({data}) {
  const { body } = data.contentfulBlogpost
  


  return (
    <Layout>
      {/* <span className="blog-template-date">{body.references[0].updatedAt}</span> */}
       <div>{body && renderRichText(body, options)}</div>
     
    </Layout>
  )
}
